import React from 'react';
import ajouter from '../image/plus.png'
import effacer from '../image/effacer.png';
import modifier from '../image/modifier.png';
import triste from '../image/triste.png'
import content from '../image/content.png'
import down from '../image/sort-down.png'


class Recette extends React.Component {

  constructor(props) {
    super(props);
    this.Modifier = this.Modifier.bind(this);
    this.Enregistrer = this.Enregistrer.bind(this);
    this.state = {
      modifier: true,
      message: "rien",
      currentRecipe: { id: 1, titre: '', ingredients: [] },
      titre: '',
      ingredients: []
    };
  }
  Modifier(recipe) {
    this.setState({ modifier: true, currentRecipe: recipe, titre: recipe.titre, ingredients: recipe.content });
    document.getElementById('recipient-name').value = recipe.titre
    recipe.content.map(r=>{
      document.getElementById('message-text').value+=r+'\n'
    })
  }
  Conf(recipe) {
    this.setState({ modifier: true, currentRecipe: recipe, titre: recipe.titre, ingredients: recipe.content });
  }
  sleep(ms) {return new Promise(resolve => setTimeout(resolve, ms)); }

  Enregistrer() {
    if (this.state.ingredients== 0 || this.state.titre == '') {
      this.setState({ message: "non", titre: this.state.currentRecipe.titre, ingredients: this.state.currentRecipe.content })
    }
    else {
      this.state.currentRecipe.titre = this.state.titre;
      this.state.currentRecipe.content = this.state.ingredients;
      this.state.modifier ?this.setState({ message: "ouii" }) : this.setState({ message: "oui" })
      this.props.Eng(this.state.currentRecipe);
      this.setState({ titre: "", ingredients: [] });
    } document.getElementById('recipient-name').value = ""
    document.getElementById('message-text').value = ""
    this.sleep(4000).then(() => { this.setState({ message: "rien" }) });
  }
  Supprimer = (recipe) => {this.props.Supp(recipe); this.Vider(); this.setState({ titre: "", ingredients: [] });}
  Vider() {
    document.getElementById('recipient-name').value = ""
    document.getElementById('message-text').value = ""
  }
  Ajouter() {
    this.setState({ modifier: false });
    if (this.props.recipes.length > 0) {
      let ids = this.props.recipes.map(a => a.id);
      let largestID = Math.max.apply(Math, ids);
      this.setState({ currentRecipe: { id: largestID + 1, titre: '', content: [] } });
    }
    else {
      this.setState({ currentRecipe: { id: 1, titre: '', content: [] } });
    }
  }
  
  ChangeTitre(currentRecipe, event) {
    this.setState({ titre: event.target.value });
  }
  ChangeIngredients(currentRecipe, event) {
     var e = event.target.value.replace(/\n\n+/g, '\n');
    if (e.substring(e.length - 1, e.length) === "\n") {
      this.setState({ ingredients: e.substring(0, e.length - 1).split('\n') });
    }
    else {
      this.setState({ ingredients: e.split('\n') });
    }
  /*  var e = event.target.value.replace(/,,+/g, ',');
    if (e.substring(e.length - 1, e.length) === ",") {
      this.setState({ ingredients: e.substring(0, e.length - 1).split(',') });
    }
    else {
      this.setState({ ingredients: e.split(',') });
    }*/
  }

  render() {
    const  num= this.props.recipes.map(recipe => <div key={recipe.id}>{recipe.id}</div>).length
   
    return (
      <div className="App-body" >

        <div> 
          <img alt="img"className=" Ajouter " src={ajouter}
         data-toggle="modal" data-target="#exampleModal"  data-backdrop="static" data-keyboard="false"
          style={{ cursor: 'pointer' }} onClick={this.Ajouter.bind(this, this.state.currentRecipe)}
          titre="Ajouter une recette" /></div>
              <p className="N"> {this.state.message === "non" ?
              <div className="alert alert-danger" role="alert">
                vous devez saisir tous les données
               </div> :
              this.state.message === "oui" ?
                <div className="alert alert-success" role="alert">
                  La recette a été ajoutée avec success
                </div>
                : this.state.message === "ouii" ?
                  <div className="alert alert-success" role="alert">
                    La recette a été modifiée avec success
                </div>
                  : null}</p>
        {this.props.recipes.length > 0 ?

          <div>
        
            <p className="Nbr">
              Nombre de recette : {num}</p>
            {this.props.recipes.map((recipe) =>
              <div  key={recipe.id}className="bod" >
                <div className="panel-group">
                  <div className="panel panel-default">
                    <div className="panel-heading">
                      <h4 className="panel-title">
                        <div style={{ cursor: 'pointer' }} className="a" data-toggle="collapse" href={'#' + recipe.id}>
                          {recipe.titre+" ("+recipe.content.length+")"}
                          <img alt="img" className="iconU" src={down} style={{ cursor: 'pointer' }} />
                        </div>
                        <div>
                          <img alt="img" className="iconM" src={modifier} style={{ cursor: 'pointer' }}
                            onClick={this.Modifier.bind(this, recipe)} data-toggle="modal" data-target="#exampleModal" />
                          <img data-toggle="modal" data-target="#exampleModalCenter" alt="img" className="icon" src={effacer} style={{ cursor: 'pointer' }}
                     onClick={this.Conf.bind(this, recipe)}  /></div>  { /* onClick={this.Supprimer.bind(this, recipe)}*/}   
                      </h4>
                    </div>
                    <div id={recipe.id} className="panel-collapse collapse">
                      <ul className="list-group">
                        {recipe.content.map((c) => <li className="list-group-item">{c}</li>)}

                      </ul>
                    </div>
                  </div>

                </div>
                <hr />
              </div>
            )
            } </div>
          :
          <h4 className="pasR">Il n'y a pas de recette  <img alt="img"  src={triste} />   Mais vous pouvez ajouter une <img  alt="img" src={content} />
          </h4>
        }
          {/* Modal */}
         

<div class="modal fade bd-example-modal-sm" id="exampleModalCenter" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
  <div class="modal-dialog modal-sm mdd" role="document">
    <div class="modal-content modal-content1 ">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLongTitle">La confirmation de suppression</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
      Voulez‑vous vraiment supprimer la recette : {this.state.currentRecipe.titre}?
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-success" data-dismiss="modal">Non</button>
        <button type="button" class="btn btn-danger" data-dismiss="modal" onClick={this.Supprimer.bind(this, this.state.currentRecipe)}>Oui</button>
      </div>
    </div>
  </div>
</div>
            {/* Modal */}
        {/* Modal */}
        <div className="modal fade" id="exampleModal" >
          <div className="modal-dialog"  >
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title" id="exampleModalLabel">{(this.state.modifier) ?
                  "Modifier la recette" : "Ajouter une recette"}</h5>
                <button type="submit" onClick={this.Vider} className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <form >
                  <div className="form-group">
                    <label htmlFor="recipient-name" className="col-form-label">titre:</label>
                    <input type="text" className="form-control" id="recipient-name"
                      placeholder="entrer le titre " defaultValue={this.state.currentRecipe.titre}
                      onChange={this.ChangeTitre.bind(this, this.state.currentRecipe)} />
                  </div>
                  <div className="form-group">
                    <label htmlFor="message-text" className="col-form-label">Ingredients:</label>
                    <textarea className=" form-control text" id="message-text" defaultValue={this.state.currentRecipe.content}
                      placeholder={"ingredient 1"+"\n"+"ingredient 2"+"\n"+"....."}
                      onChange={this.ChangeIngredients.bind(this, this.state.currentRecipe)}></textarea>
                  </div>
                  <div className="modal-footer">
                    <input type="submit" className="btn btn-info" data-dismiss="modal" onClick={this.Enregistrer} value="Enregistrer" />
                    <input type="reset" className="btn btn-danger" data-dismiss="modal" onClick={this.Vider} value="annuler" />
                  </div>  </form>
              </div>
            </div>
          </div>
        </div>
         {/* Modal */}

      </div>
    );
  }
}


export default Recette;